import { Row, Col, } from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom'
import Card from '../Card'
import AOS from 'aos';

import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import "../landing-page/home.css"

import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { authLocations, dashboardLocations } from '../../router/fws-path-locations';
import { loginUser } from '../../store/actions/auth-actions';
import { IAuthState } from '../../store/Models/AuthState';
import HomeHeader from '../landing-page/header';
import HomeFooter from '../landing-page/footer';
import Loader from '../Loader';
import FwsLoader from '../loader/fws-loader';


const SignIn = () => {
    let history = useHistory();
    const dispatch = useDispatch();
    
    const state = useSelector((state: any) => state);
    const { message } : IAuthState = state.auth;

    var token = sessionStorage.getItem('token');
    var user = sessionStorage.getItem('user')
    

    useEffect(() => {
        AOS.init({
            duration: 1000,
            easing: 'ease-in-out',
            once: true,
            mirror: false
        });
   
      }, [])

    useEffect(() => { 
        if (token) {
            if (JSON.parse(user || '').userType === '0') {
                window.location.href = '/dashboard';
            } else {
                window.location.href = '/client-dashboard';
            }

        }
    }, [token, history, user])

    const validation = Yup.object().shape({
        userName: Yup.string()
            .min(2, 'Username Too Short!')
            .max(50, 'Username Too Long!')
            .required('Username is required to login'),
        password: Yup.string().required("Password Required")
            .min(4, 'Password must be a minimum of 4 characters'),
    });

    return (
        <>
        <HomeHeader/>
        
            <section className="login-content" style={{background: '#fcfcfc'}} data-aos="fade-up">
                <FwsLoader />
                <Row className="m-0 align-items-center d-flex justify-content-center  vh-100">
                    <Col md="6">
                        <Row className="justify-content-center">
                            <Col md="10">
                                <Card className="d-flex justify-content-center mb-0 auth-card">
                                    <Card.Body>
                                        {/* <Link to={dashboardLocations.dashboard} className="navbar-brand d-flex align-items-center mb-3">
                                            {/* <Logo color={true} /> */}
                                            {/* <h4 className="logo-title ms-3">FLAVTECH</h4> 
                                        </Link> */}
                                        <h2 className="mb-2 text-center fw-bold title">Sign In</h2>
                                        <p className="text-center description">Login to stay connected.</p>

                                        <Formik
                                            initialValues={{
                                                userName: '',
                                                password: '',
                                            }}
                                            validationSchema={validation}
                                            onSubmit={values => {
                                                loginUser(values)(dispatch)
                                            }}
                                        >
                                            {({
                                                handleChange,
                                                handleBlur,
                                                handleSubmit,
                                                values,
                                                touched,
                                                errors,
                                                isValid }) => (

                                                <Form >
                                                    <Row>
                                                        {message && <div className='text-danger'>{message}</div>}
                                                        <Col lg="12">
                                                            <div className="form-group">
                                                                {((touched.userName && errors.userName) || message) && <div className='text-danger'>{errors.userName}</div>}
                                                                <label htmlFor="userName" className="form-label">User Name</label>
                                                                <Field type="userName" className="form-control" name="userName" id="userName" aria-describedby="userName" required placeholder=" " />
                                                            </div>
                                                        </Col>
                                                        <Col lg="12" className="">
                                                            <div className="form-group">
                                                                {(touched.password && errors.password) && <div className='text-danger'>{errors.password}</div>}
                                                                <label htmlFor="password" className="form-label">Password</label>
                                                                <Field type="password" required className="form-control" name="password" id="password" aria-describedby="password" placeholder=" " />
                                                            </div>
                                                        </Col>
                                                        <Col lg="12" className="d-flex justify-content-between">
                                                            <div className="form-check mb-3 form-Check">
                                                                <Field type="checkbox" id="customCheck1" className="form-check-input" />
                                                                <label htmlFor="customCheck1" className='check-label'>Remember Me </label>
                                                            </div>
                                                            <Link to={authLocations.register}>Sign Up</Link>
                                                            <Link to={authLocations.firstTimeLogin}>Forgot Password?</Link>
                                                        </Col>
                                                    </Row>
                                                    <div className="d-flex justify-content-center">
                                                        <button onSubmit={() => {
                                                            handleSubmit()
                                                        }} type="submit"  className='btn btn-primary'>Sign In</button>
                                                    </div>
                                                    {/* <p className="mt-3 text-center">
                                                        Don’t have an account? <Link to="/auth/sign-up" className="text-underline">Click here to sign up.</Link>
                                                    </p> */}
                                                </Form>
                                            )}
                                        </Formik>

                                    </Card.Body>
                                </Card>
                            </Col>
                        </Row>
                        <div className="sign-bg">
                            <svg width="280" height="230" viewBox="0 0 431 398" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <g opacity="0.05">
                                    <rect x="-157.085" y="193.773" width="543" height="77.5714" rx="38.7857" transform="rotate(-45 -157.085 193.773)" fill="#3B8AFF" />
                                    <rect x="7.46875" y="358.327" width="543" height="77.5714" rx="38.7857" transform="rotate(-45 7.46875 358.327)" fill="#3B8AFF" />
                                    <rect x="61.9355" y="138.545" width="310.286" height="77.5714" rx="38.7857" transform="rotate(45 61.9355 138.545)" fill="#3B8AFF" />
                                    <rect x="62.3154" y="-190.173" width="543" height="77.5714" rx="38.7857" transform="rotate(45 62.3154 -190.173)" fill="#3B8AFF" />
                                </g>
                            </svg>
                        </div>
                    </Col>
                    {/* <Col md="6" className="d-md-block d-none bg-primary p-0 mt-n1 vh-100 overflow-hidden">
                        {/* <Image src={auth1} className="Image-fluid gradient-main animated-scaleX" alt="images" />
                    </Col> */}
                </Row>
            </section>
          
            {/* <HomeFooter/> */}
        </>
    )
}

export default SignIn
